.reserv {
  // width: 180px;
  padding: 0;
  margin: 0 auto;
  margin-top: 10px;
  font-size: 14px;
  line-height: 26px;
  font-weight: 400;
  text-align: center;
  color: #aaaaaa;
}

.reserv__text {
  position: relative;
  display: block;
  width: 310px;
  padding: 0;
  margin: 0 auto;
  // color: #aaaaaa;
  color: inherit;
  text-transform: none;
  text-decoration: none; /*   */
  border-top: 1px solid #aaaaaa;
  cursor: default; /*   */

  // &:hover::after {
  //   width: 100%;/*  ::after     */
  // }
}

