@media (min-width: $tablet-width) {

  .reserv {
    // width: 50%;
    padding: 5px 0;
    margin: 0 auto 10px;
    // margin-right: 150px;
    font-size: 14px;
    line-height: 26px;
    font-weight: 400;
    text-align: center;
  
    // border-top: 1px solid #aaaaaa;
  }

  .reserv__text {
    width: 350px;
    text-transform: uppercase;    
    border-top: none;
  }
}


@media (min-width: $desktop-width) {

  .reserv {
    font-size: 16px;
    line-height: 26px;
    font-weight: 400;
  
    // border-top: 1px solid #aaaaaa;
  }

  .reserv__text {
    width: 500px;

    text-transform: uppercase;
  }

}
