.services {
  margin: 0 auto;
  margin-bottom: 10px;
  margin-top: 10px;
  // width: 320px;
}

.services__container {
  position: relative;
  display: flex;
  align-items: center;
  margin: 0 auto;
  width: 300px;
}

.services__block {
  display: flex;
  justify-content: center; 
  align-items: center;
  flex-wrap: wrap;
  text-align: center;
  // margin: 10px;
  margin: 15px 10px;
  // padding: 3px;
  // margin: 0;
}

.service__link {
  position: relative;
  display: inline-block;
  width: auto;
  height: auto;
  min-width: 168px;
  background-color: transparent;  
  margin: 0px 25px 10px;
  margin-left: 0px;
  // border: none;
  
  border: 1px solid  #9a9ca0;
  border-radius: 6px 0px;
  transition: 0.2s;
  cursor: pointer;

    // /*  cursor: pointer     -   /
    &:not([href="#"]) {
      cursor: pointer;
    }

    &:hover,
    &:focus {
      color: #fff;
      // color: rgb(255,255,255);
      transition: 0.2s 0.1s;
  }

    &:active {
      color: #fff;
    // opacity: 0.5;
    opacity: 0.6;
  }

    &::before {
      position: absolute;
      content: "";
      // background-color: rgb(28, 31, 30);
      background-color: #9a9ca0;
      transition: 0.3s ease-out;
  }

      &::after {
        position: absolute;
        content: "";
  }
}

.service__link span {
  position: relative;
  display: inline-block;
  text-align: center;
  font-size: 14px;
  font-weight: bold;
  letter-spacing: 2px;
  text-transform: uppercase;
  top: 0;
  left: 0;
  width: 100%;
  padding: 10px 15px;
  // padding: 15px 20px; 
  color:  #9a9ca0;  
  // border: 1px solid  #9a9ca0;
  // // color: rgb(28, 31, 30);
  // // border: 1px solid rgb(28, 31, 30);
  // border-radius: 5px 0px;
  // transition: 0.2s;
} 

.service__link span:hover {
  color: rgb(255,255,255);
  transition: 0.2s 0.1s;
}

  /* hover-slide-right */

  .service__link.service__link--hover-slide-right::before {
    position: absolute;
    top: 0;
    bottom: 0;
    left: 0;
    height: 100%;
    width: 0%; 
  }

  .service__link.service__link--hover-slide-right:hover::before {
    width: 100%;
  }


@media (min-width: $tablet-width) {

  .services { 
    margin: 0 auto;
  }

  .services__container {
    width: 768px;
    margin: 0 auto;    
  }

  .services__block {
    justify-content: flex-start;
    margin-left: 60px;
    margin-bottom: 0px;
  }

  // .service__link span {
  //   border-left: 1px solid  #9a9ca0;
  //   border-top: unset;
  //   border-bottom: unset;
  //   border-right: unset;
  //   border-radius: unset;
  // } 

  .service__link {
    position: relative;
    display: inline-block;
    width: auto;
    height: auto;
    min-width: 180px;
    background-color: transparent;  
    margin: 0px 25px 10px;
    margin-left: 0px;
    // border: none;
    border-left: 1px solid  #9a9ca0;
    border-top: unset;
    border-bottom: unset;
    border-right: unset;
    border-radius: unset;

    transition: 0.2s;
    cursor: pointer;
  }

}


@media (min-width: $desktop-width) {

  .services { 
    margin-bottom: 10px;
  }

  .services__container {
    width: 1200px;  
  }

}
